'use strict';

console.log("Loading file:card-list.js ...");/*global document:false, sessionStorage: false, console: false, alert: false, $: false, window: false, jQuery:false,  location:false, debugger:false, navigator:false, localStorage:false,google: false */

var $ = require('jquery');

// const log = require('../lib/log');
const Display = require('../lib/display');

const Data = require('../services/data');

const id="card-list";
const template = "<div class=\"ui link cards\"> {{#each this}} {{>word-card this}} {{/each}}</div>";


const AbstractRefreshTab = require('../lib/view/abstract-refresh-tab');
const SINGLETON= class CardList extends AbstractRefreshTab{

    constructor(){
		super("card-list");
    }


    initialize(){
        return Display.compile(id,template);
    }


    display(){
        
        this.setLoading(true);
        var $el=this.$el;
        
        const data=Data.getWords();
        
        var content=Display.render(id,data);
        
        $el.html(content);
        
        this.setLoading(false);
    }
};

module.exports = new SINGLETON();
