console.log("Loading file:play-sound.js ...");/*global document:false, sessionStorage: false, console: false, alert: false, $: false, window: false, jQuery:false,  location:false, debugger:false, navigator:false, localStorage:false,google: false */

const $q = require('q');

const El = require('../lib/element');

const AbstractPartial = require('../lib/partial/abstract-partial');

const SINGLETON=class PlaySound extends AbstractPartial {

    constructor(){
		super("play-sound");

        this.cache={};
    }


    initialize(){
        El.clickEvent(".play",(e)=>{
            El.eventStop(e);

            const id=El.getDataId(e,null);

            this.play(id);
        });
    }


    play(id=null){
        if (id){
            
            let audio=this.cache[id];
            
            if (! audio){
                const path=`./img/data/${id}.mp3`;

                // alert("Path:"+path);

                audio = new Audio(path);
                this.cache[id]=audio;
            }
            audio.play();

            // alert("Sound:"+path);
        }
    }
};


module.exports = new SINGLETON();

