'use strict';

console.log("Loading file:xiao-partial.js ...");/*global document:false, sessionStorage: false, console: false, alert: false, $: false, window: false, jQuery:false,  location:false, debugger:false, navigator:false, localStorage:false,google: false */

const $q = require('q');

const Obj = require('../lib/obj');
const Log = require('../lib/log');
const Display = require('../lib/display');



const TEMPLATES=[

   {
	   id:"word-card",
       content:"<div class=\"ui card\" data-id=\"{{id}}\"><div class=\"image\"> <img src=\"{{>img-src . }}\" onerror=\"this.onerror=null; this.src=\'./img/data/default.png\'\"></div><div class=\"content\"><div class=\"ui header center-text huge\">{{char}}</div><div class=\"ui meta center-text capitalize\"><a class=\"play\" data-id=\"{{id}}\"><i class=\"volume up icon\"></i> {{pinyin}}</a></div></div><div class=\"extra content\"> {{#each lang}}<div class=\"capitalize\"><a class=\"play\" data-id=\"{{../id}}-{{@key}}\"><i class=\"{{@key}} flag\"></i><i class=\"volume up icon\"></i> {{this}}</a></div> {{/each}}</div></div>"
   },

   {
	   id:"word-list",
       content:"{{#each this}} {{>word-card this}} {{/each}}"
   },

   {
	   id:"img-src",
       content:"./img/data/{{id}}.png"
   },

];


const SINGLETON=class Partials  {
    constructor(){        
    }    


    display(id,data=null){
        const content=Display.render(id,data);
        
        return content;
    }

    init(){
	    Log.info("Loading templates ...");
	    const promisses=[];
	    const that=this;

	    TEMPLATES.forEach( (el,index) => {
		    Log.info("Compiling partial:"+el.id);


            // {{word-card DATA }}
            Display.registerHelper(el.id, (content) =>{
                return that.display(el.id,content);
            });

            // {{> word-card DATA }}
		    promisses.push(Display.registerPartial(el.id,el.content));

            
	    });
        
	    return $q.all(promisses);
    }
    

}


module.exports=new SINGLETON();
